import React from 'react'
import './Login.css'
import store from '../../redux'

class Login extends React.Component {

   constructor () {
      super();
      this.confirm = React.createRef();
      this.user = React.createRef();
      this.password = React.createRef();
   }

   state = {
      confirmCount: 4,
      canvasValue: [1, 2, 3, 4, 5, 6, 7, 8, 9,'a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'],
      res: ''
   }

   componentDidMount () {
      this.reconfirm();
   }

   render () {
      return (
         <div className='login'>       
            <div className='content'>
               <div className='cancel' onClick={this.cancel}></div> 
               <div className='title'>欢迎用户{ this.props.loginOrRegiste === 'login' ? '登录' : '注册' }</div>
               <div className='user'>
                  <label htmlFor="usr">用户名:&nbsp; </label>
                  <input ref={this.user} type='text' id='usr' placeholder="请输入用户名"></input>
               </div>
               <div className='password'>
                  <label htmlFor='psd'>密&nbsp;&nbsp;码:&nbsp; </label>
                  <input ref={this.password} type='password' id='psd' placeholder='请输入密码'></input>
               </div>
               <div className='confirm'>
                  <canvas ref={ (dom) => {this.canvas = dom}} id='canvas' width='140' height='40'></canvas>
                  <span onClick={this.reconfirm}>看不清,换一张</span><br></br>
                  <input ref={this.confirm} placeholder='请输入验证码'></input>
               </div>
               <button className='over' onClick={this.confirmHandler}>{ this.props.loginOrRegiste === 'login' ? '登录' : '注册' }</button>
            </div>
         </div>
      )
   }


   confirmHandler = () => {
      var confirm = this.confirm.current.value;
      var user = this.user.current.value;
      var password = this.password.current.value;
      var state = store.getState();
      var loginOrRegiste = state.loginOrRegiste;

      if (loginOrRegiste === 'login') {
         if (!user) {
            alert('用户名不能为空');
            return;
         }

         if (!password) {
            alert('密码不能为空');
            return;
         }         
      }

      if (!confirm) {
         alert('请填写验证码');
         return;
      }

      if (this.state.res !== confirm) {
         alert('验证码错误')               //发送验证请求 /check
         return;
      }

      var obj = {
         user, password,
         loginOrRegiste: state.loginOrRegiste
      }

      this.fetchData(obj);
      fetch('/isLogin', {
         method: 'GET',
         headers: {
            'Content-Type': 'application/json; charset=utf-8'
         },
         credentials: 'include'
      }).then( (res) => {
         // console.log('first -> isLogin', res.json())
         store.dispatch({
            type: 'getUser',
            value: user
         })
      })
   }  

   fetchData (obj) {
      fetch('/login', {
         method: 'POST',
         headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json'     
         },
         body: JSON.stringify(obj)
      })
      .then( (response) => {
         return response.json();
      })
      .then( (data) => {
         if (obj.loginOrRegiste === 'login') {
            if (data.successLogin) {
               alert('登录成功');
            } else {
               alert('用户不存在/密码错误')
               return;
            }            
         } else {
            alert('注册并登录成功')
         }

         store.dispatch({
            type: 'changeLoad',
            value: false
         })
         store.dispatch({
            type: 'changeLogin',
            value: data.successLogin
         })

      })
   }

   reconfirm = () => {
      var ctx = this.canvas.getContext('2d');
      var h = this.canvas.height * 6 / 7;
      var w = this.canvas.width;
      var count = this.state.confirmCount;
      var arr = this.state.canvasValue;
      var len = this.state.canvasValue.length;

      ctx.fillStyle = 'orange';
      ctx.font = '40px 楷体';
      ctx.clearRect(0, 0, w, h*7/6);
      var countRes = '';
      var res = '';
      for (var i = 0; i < count; i++) {
         res = arr[Math.floor( Math.random()*(len) )];
         ctx.save();
         ctx.rotate(Math.random()*5*Math.PI/180);
         ctx.fillText(res, w * i / count + 10, h);
         ctx.restore();
         countRes += res;
      }
      this.setState({
         res: countRes
      })
   }

   cancel = () => {
      store.dispatch({
         type: 'changeLoad',
         value: false
      })
   }

}


export default Login