

module.exports = function reducer (state, action) {
   var newState = JSON.parse( JSON.stringify(state) );
   
   switch(action.type) {
      case 'changeLoad':                    //是否进入登录状态
         newState.isLoad = action.value
         break;
 
      case 'loginOrRegiste':                // login | registe
         newState.loginOrRegiste = action.value;
         break;
 
      case 'changeLogin':                  //是否登录，-》保持登录状态
         newState.successLogin = action.value;
         break;

      case 'getUser':
         newState.user = action.value;
         break;

      default: 
         break;
   }

   return newState;
}