import React from 'react'
import LeaveWords from  './LeaveWords/LeaveWords';
import './Footer.css';
import store from '../../redux/index';

class Footer extends React.Component {
   constructor () {
      super();
      this.mess = React.createRef();
      this.name = React.createRef();
      this.mail = React.createRef();
   }

   state = {
      wrongStyle: {
         border: '1px solid red',
         boxShadow: '0 0 3px 3px rgba(255, 0, 0, .4)'
      },
      changeStyle: {},
      leave_words: {
         origin: [],
         target: []
      }
   }


   render () {
      return (
         <div className='footer'>
            <div className='leave-words'>
               <textarea ref={ this.mess } style={ this.state.changeStyle } onFocus={ this.focusHandler } placeholder='任何建议,请留言让我知道' className='leave-down'></textarea>
               <div className='message'>
                  称呼：&nbsp;<input ref={ this.name } placeholder='可不填..'></input><br></br>
                  邮箱：&nbsp;<input ref={ this.mail } placeholder='可不填..' className='mes-mail'></input>
               </div>
               <button className='submit' onClick={this.putMessage}>留言</button>
            </div>
            <LeaveWords 
            user={ store.getState().user } 
            leave_words={ this.state.leave_words } 
            getDate={ this.getDate }
            ></LeaveWords>
         </div>
      )
   }

   componentDidMount () {
      this.fetchList();
      window.document.body.onbeforeunload = () => {
         this.putList(this.state.leave_words );
      }
   }

   focusHandler = () => {
      this.setState({
         changeStyle: {}
      })
   }
 
   putMessage = () => {                  //提交信息
      var origin = this.state.leave_words.origin;
      const info = {
         message: this.mess.current.value,
         name: this.name.current.value,
         mail: this.mail.current.value
      }  
 
      if ( !info.message ) {            //修改错误样式
         this.setState({
            changeStyle: this.state.wrongStyle
         })
         alert('请填写您的建议再提交');
         return;
      }

      origin.push({
         user: store.getState().user,
         value: info.message,
         date: this.getDate(),
         next: []
      })

      this.mess.current.value = '';
      this.name.current.value = '';
      this.mail.current.value = '';

      this.setState({
         leave_words: this.state.leave_words
      })

   }

   getDate = () => {                 //获取时间
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();

      return `${year}-${month}-${day}`
   }

   putList = (info) => {                //提交信息到后台
      
      fetch('/putLeaveWords', {
         method: 'POST',
         headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json'     
         },
         body: JSON.stringify(info)
      })
   }

   fetchList = () => {                    //从后台拿数据
      fetch('/getLeaveWords', {
         method: 'GET',
         headers: {
            'Accept': 'application/json'
         }
      })
      .then( (response) => {
         return response.json();
      })
      .then( (data) => {
         this.setState({
            leave_words: data.list    //{origin: array, target: array}
         })
      })
   }

}

export default Footer