import React from 'react'
import Login from './Login';
import store from '../../redux/index';
import HasLogin from './hasLogin';

class HigerLogin extends React.Component {

   state = {
      ...store.getState()
   }

   componentDidMount = () => {
      store.subscribe(() => {
         this.setState({
            ...store.getState()
         })
      })
      
   }

   render () {
      return (
         <React.Fragment>
            
            {
               (() => {

                  if (this.state.isLoad) {         //登录状态

                     if (this.state.successLogin) {               //用户已登录
                        return <HasLogin></HasLogin>

                     } else if (this.state.loginOrRegiste === 'login') {        //用户准备登录
                        return <Login loginOrRegiste={this.state.loginOrRegiste}></Login>

                     } else if (this.state.loginOrRegiste === 'registe') {      //用户准备注册
                        return <Login loginOrRegiste={this.state.loginOrRegiste}></Login>                //已登录仍提示
                     }

                  } 

                  // this.state.isLoad ? (store.getState().successLogin ? <HasLogin></HasLogin> : <Login loginOrRegiste={this.state.loginOrRegiste}></Login>) : ''
               })()
               
            }

         </React.Fragment>
      )
   }
}





export default HigerLogin