import React from 'react'

import './Aboutme.css';

class Aboutme extends React.Component {
   state = {
      infomation: {
         name: '殷杰',
         skill: 'web前端',
         birth: '96-12-18',
         school: '合肥工业大学宣城校区',
         hobby: '乒乓球,羽毛球',
         wechat: 1926529213,
         mail: 'forlaugh@126.com' 
      }
   }

   render () {
      return (
         <div className='aboutme'>
            <div className='content'>
               <div className='name'>姓名：{ this.state.infomation.name }</div>
               <div className='skill'>职业技能：{ this.state.infomation.skill }</div>
               <div className='school'>毕业学校：{ this.state.infomation.school }</div>
               <div className='hobby'>业余爱好：{ this.state.infomation.hobby }</div>
               <div className='wechat'>微信号：{ this.state.infomation.wechat }</div>
               <div className='mail'>邮箱：{ this.state.infomation.mail }</div>
            </div>
            <div className='tosay'>
               一个业余的技术人员，在前端小白的路上越走越远。<br/>
               渴望有一天可以被称为大神，先定个小目标，被称为大神<br/>
               另外本人很愿意和各路人士交朋友。
            </div>
         </div>
      )
   }
}



export default Aboutme