import React from 'react'
import './LeaveWords.css'

class LeaveWords extends React.Component {
   constructor () {
      super();

      this.comment_items = [];
      // this.fetchList();
   }

   /* 
      a 留言说：
         a说发奋读书放大范德萨地方
      b 回复a说：
         dfdafdfadsa
      c 回复说：
         幅度萨芬记得啦开发的
   */
   
   state = {
      fresh: 0,          //刷新页面
      isComment: [],      //控制输入框开关
      commentIndex: 0
   }


   render () {
      return (
         <div className='leave-words-list'>
            <ul>
               {
                  (() => {
                     var state = this.state;
                     var props = this.props;
                     var origin = props.leave_words.origin;
                     var target = props.leave_words.target;
                     var liList = [];
                     origin.forEach( (ele, index) => {
                        var str = <div className='list-item'>
                           <div id='list-item-main'>
                              <p className='list-item-info'><b>{ ele.user }</b>&emsp;留言说： &ensp;&ensp;{ ele.date }</p>
                              <p className='list-item-value'>{ ele.value }</p> 
                              <button className='list-item-btn' onClick={ () => {this.joinComment(index)} }>{ state.isComment[index] ? "取消评论" : "参与评论" }</button>  
                              {                           
                                 <div style={{ display: (state.isComment[index] ? 'block' : 'none') }}>
                                    <textarea ref={ dom => { this.comment_items[index] = dom }} rows='4' className='join-comment' placeholder='感谢您积极参与评论'></textarea>
                                    <button className='comment-submit' onClick={ () => {this.commentSubmit(index)} }>提交</button>
                                 </div>
                              }                     
                           </div>
                           {
                              ele.next.map( (val, innerIndex) => {
                                 var mes = target[val];
                                 return (<div key={ innerIndex }>
                                    <p className='list-item-info'><b>{ mes.user }</b>&emsp;回复说：&ensp;&ensp;{ mes.date }</p>
                                    <p  className='list-item-value'>{ mes.value }</p>
                                 </div>)
                              })
                           }
                        </div>;
                        str = <li key={ index }> { str } </li>
                        liList.push(str);
                     })
                     return liList;
                  })()
               }
            </ul>
         </div>
      )
   }

   joinComment = (index) => {

      var isComment = this.state.isComment;
      isComment[index] = !isComment[index];

      this.setState({
         isComment: isComment
      })
   }

   commentSubmit = (index) => {
      var props = this.props;
      var date = props.getDate();
      var user = props.user;
      var leave_words = props.leave_words;
      var len = leave_words.target.length;
      var item = this.comment_items[index];

      if (item.value === '') {
         item.classList.add('active');
         setTimeout( () => {
            item.classList.remove('active');
         }, 3000)
      }

      var comment = {
         user, date,
         value: item.value
      }

      item.value = '';

      leave_words.origin[index].next.push(len);
      leave_words.target.push(comment);
      
      this.joinComment();
   }

}



export default LeaveWords