import React from 'react'
import store from '../../redux'

class hasLogin extends React.Component {
   state = {

   }

   componentDidMount () {
      setTimeout(this.cancel, 1000);
   }

   render () {
      return (
         <div className='login'>
            <div className='content'>
               <div>已登录</div>
            </div>
         </div>
      )
   }

   cancel = () => {
      store.dispatch({
         type: 'changeLoad',
         value: false
      })
   }

}



export default hasLogin