import React from 'react'
import { 
   NavLink
} from 'react-router-dom'

import './Head.css'
import store from '../../redux';
import nameImg from '../../img/name.jpg'

class Head extends React.Component {
   state = {
      loginPath: '/login'
   }

   render () {
      return (
         <div className='head'>
            <div className='title'>
               <img src={nameImg} alt='恒念博客'/>
            </div>
            <div className='nav'>
               <NavLink exact to='/'>首页</NavLink>
               <NavLink to='/notes'>记录</NavLink>
               <NavLink to='/works'>分享</NavLink>
               <NavLink to='/aboutme'>关于我</NavLink>                        
            </div>
            <div className="headlogin">
               <span onClick={this.login}>登录</span>/<span onClick={this.registe}>注册</span>
            </div>
         </div>
      )
   }

   login = () => {
      store.dispatch({
         type: 'loginOrRegiste',
         value: 'login'
      })
      store.dispatch({
         type: 'changeLoad',
         value: true
      })
   }

   registe = () => {
      store.dispatch({
         type: 'loginOrRegiste',
         value: 'registe'
      })
      store.dispatch({
         type: 'changeLoad',
         value: true
      })
   }

}



export default Head