import React from 'react'
import './Works.css'

class Works extends React.Component {

   constructor () {
      super();
   }

   componentDidMount () {
      const path = '/notes';
      fetch(path).then( (response) => {
         return response.json()
      }).then( (data) => {
         this.setState({
            fileData: data        //{path, description}
         })
      })
   }

   state = {
      fileData: []
   }

   render () {
      return (
         <div className='works'>
            <ul className='container'>
            {
               this.state.fileData.map( (ele, index) => (
                  <li className='col-lg-3 col-md-4 col-sm-6' key={index}>
                     <a href={`${ele.path}`} target='_blank'>
                        {ele.description}
                     </a>
                  </li>
               ))
            }
            </ul>
         </div>
      )
   }

}



export default Works